* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 100px;
  color: #fff;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  min-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  background-color: #f00;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
}

.block--4 {
  background-color: #600;
}

.block--5 {
  background-color: #300;
}

.block--6 {
  background-color: #000;
}

@media (min-width: 600px) and (max-width: 899px) {
  .block--3 {
    order: 1;
  }
  .block--5 {
    order: 2;
  }
  .block--6 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    width: 33%;
  }
  .block--4 {
    order: 2;
  }
  .block--5 {
    order: 1;
  }
}
